*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

h1{
  margin-bottom: 20px;
  background-color: #000;
  color: #fff;
  padding: 10px;
}
body{
  background-color: #f6f6f6;
  text-align: center;
}
body, h1,h2, button , input{
    font-family: 'Courier New , Courier, monospace'
}
.form input{
  outline:none;
}
.form input, .form button {
    width: 200px;
    height: 40px;
    border-radius: 2px;
    margin: 2px;
    padding: 5px ;
    border: 1px solid #888;
}

button{
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
}

.post{
  width: 70%;
  padding: 10px;
  border: 1px solid #666;
  background-color: #fff;
  margin: 10px auto;
}

.post button{
    width: 150px;
    display: inline-block;
    margin: 5px;
}